import { HUtils, MyLib, MyUI } from "@macashipo/mlib"
import React, { Fragment, useEffect, useRef } from "react"
import useConfigPage from "../../hooks/useConfigPage"
import Tabs from "react-responsive-tabs"
import styled from "styled-components"
import "./Styles/admin_table.css"
import { ApiGeneric } from "src/services/apiServices"
import { fnListMyPage } from "src/helpers/fnList"

const WrapApiRequest = styled.div`
  background: #fff;
  h5 {
    font-weight: normal;
    font-size: 0.88rem;
    padding-bottom: 0.5rem;
    border-bottom: #dee2e6 solid 1px;
    margin: 0;
  }
  h4 {
    font-size: 1.1rem;
    font-weight: 300;
  }
  select {
    border: 1px solid #ced4da;
    padding-left: 0.3rem;
  }
  .a-status-bar {
    margin-left: 1rem;
    display: flex;
    align-items: center;
    height: calc(2.25rem + 2px);
    span {
      margin-right: 1.5rem;
      font-weight: 600;
    }
  }
  .body-tabs > .RRT__tab {
    padding: 0.3rem 1.25rem 0.5rem 1.25rem;
  }
  .body-tabs.body-tabs-layout {
    margin-left: 0;
    margin-right: 0;
    padding: 0rem;
  }
  .input-group-append .btn {
    border-radius: 0;
  }
  .a-query {
    .a-query-row {
      padding-left: 2rem;
      padding-right: 0.5rem;
      .custom-control-label::before {
        top: 0.85rem;
      }
      .custom-control-label::after {
        top: 0.85rem;
      }
      i {
        opacity: 0;
      }
    }
    .a-query-row:hover {
      background: #f1f1f1;
      i {
        opacity: 1;
      }
    }
  }
  .a-auth {
    .body-tabs.body-tabs-layout {
      border: none;
    }
  }
  .a-basic {
    label {
      width: 7rem;
      margin-bottom: 0;
      font-weight: 600;
    }
  }
  .a-pwd {
    i {
      position: absolute;
      right: 1.5rem;
    }
  }
  .a-bearer,
  .a-text {
    textarea {
      width: 100%;
      height: 11.5rem;
    }
  }
  .a-oauth2 {
    label {
      width: 9rem;
    }
  }
  .a-request-container {
    .form-control {
      border: none;
      border-radius: 0;
      border-bottom: #dee2e6 solid 1px;
      background: transparent;
      width: 100%;
    }
    label {
      white-space: nowrap;
      margin-bottom: 0;
      color: #000;
      min-width: 20px;
    }
  }
`

const StatusCodeArr = [
  {
    code: 200,
    colorCode: "text-success",
  },
  {
    code: 400,
    colorCode: "text-danger",
  },
  {
    code: 401,
    colorCode: "text-danger",
  },
  {
    code: 404,
    colorCode: "text-danger",
  },
  {
    code: 500,
    colorCode: "text-danger",
  },
  {
    code: 501,
    colorCode: "text-danger",
  },
  {
    code: 503,
    colorCode: "text-danger",
  },
]

export default function MP_Api_Request(props) {
  const cfp = useConfigPage(props.configPage, {
    needOptions: false,
    needList: false,
    needContents: false,
    props,
  })
  const refCode = React.useRef(null)
  const refCode2 = React.useRef(null)
  const refCodeQuery = React.useRef(null)
  const refCodeVar = React.useRef(null)
  const refQuery = React.useRef(null)
  const [vRes, setVRes] = React.useState("")
  const [responseResult, setReponseResult] = React.useState({
    StatusCode: null,
    Size: null,
    Times: null,
  })
  const [json, setJson] = React.useState()
  const [method, setMethod] = React.useState("GET")
  const [url, setUrl] = React.useState("")
  const [beareToken, setBeareToken] = React.useState("")
  const [tokenPrefix, setTokenPrefix] = React.useState("Bearer")
  const [needAddRow, setNeedAddRow] = React.useState(true)
  const [queryParams, setQueryParams] = React.useState([
    { params: "", value: "", checked: false },
  ])
  const [headers, setHeaders] = React.useState([
    { fieldName: "Acept", value: "*/*", checked: true },
    { fieldName: "", value: "", checked: false },
  ])
  const [index, setIndex] = React.useState(0)

  const usePrevious = value => {
    const ref = useRef()
    useEffect(() => {
      ref.current = value
    })
    return ref.current
  }
  const prevUrl = usePrevious({ url })

  const renderBodyJson = json => {
    const obj = json ? JSON.parse(json) : {}
    let newObj = {}
    for (var fieldName in obj) {
      newObj[fieldName] = obj[fieldName].default
    }
    return newObj
  }

  useEffect(() => {
    const propsData = cfp.fnList.getDefautR && cfp.fnList.getDefautR()
    if (propsData) {
      setJson(renderBodyJson(propsData.Parameters))
      setUrl(propsData.Url)
      setMethod(propsData.Method)
      const Authorization = JSON.parse(propsData.Authorization)
      setBeareToken(Authorization.Bearer?.value)
    }
  }, [])
  useEffect(() => {
    console.log("prevUrl", prevUrl)
    if (prevUrl?.url == "") {
      console.log("queryParams", queryParams)
      let obj = queryParams
      obj = [{ params: "", value: "", checked: false }]
      setQueryParams(obj)
    }
    console.log("queryParams111", queryParams)
  }, [url])
  const changeUrl = () => {
    let u = (url.indexOf("?") !== -1 && url.slice(0, url.indexOf("?"))) || url
    let params = ""
    queryParams.forEach((el, i) => {
      if (i == 0 && u !== "" && el.params !== "") {
        params += "?" + el.params
      }
      if (i !== 0 && u !== "" && el.params !== "") {
        params += "&" + el.params
      }
      if (u !== "" && el.params !== "" && el.value !== "") {
        params += "=" + el.value
      }
    })
    u = u + params
    setUrl(u)
  }
  const queryParamsRow = (data, index) => {
    console.log("v", data, data.params)
    const handleOnChangeQueryParams = (name, e, index) => {
      setIndex(index)
      let obj = queryParams
      obj[index][name] = e.target.value
      obj[index].checked = true
      setQueryParams(obj)
      url && changeUrl()
    }
    const handleAddQueryRow = (e, index) => {
      if (needAddRow === true && url !== "") {
        let obj = queryParams
        obj[index].checked = true
        obj.push({ params: "", value: "", checked: true })
        setQueryParams(obj)
        setNeedAddRow(false)
      }
    }
    return (
      <div className="custom-control a-query-row form-inline d-flex align-items-center flex-nowrap custom-checkbox mb-3">
        <input
          type="checkbox"
          className="custom-control-input"
          id="customCheck"
          // ref={r => (refQuery.current = r)}
          defaultChecked={data.checked}
          onClick={e => {
            let obj = queryParams
            obj[index].checked = e.target.checked
            setQueryParams(queryParams)
          }}
        />
        <label
          className="custom-control-label d-flex flex-grow-1"
          htmlFor="customCheck"
        >
          <input
            type="text"
            className="form-control mr-2"
            placeholder="parameter"
            defaultValue={data.params}
            onChange={e => handleOnChangeQueryParams("params", e, index)}
            onKeyDown={e => handleAddQueryRow(e, index)}
          />
          <input
            type="text"
            className="form-control"
            placeholder="value"
            defaultValue={data.value}
            onChange={e => handleOnChangeQueryParams("value", e, index)}
          />
        </label>
        <label className="ps-2">
          <i className="fa fa-times" aria-hidden="true"></i>
        </label>
      </div>
    )
  }
  const ContentTabQuery = () => {
    console.log("qry", queryParams)

    return (
      <div className="a-query">
        <h4>Query Parameters</h4>
        {/* <div className="custom-control a-query-row form-inline d-flex align-items-center flex-nowrap custom-checkbox mb-3">
          <input
            type="checkbox"
            className="custom-control-input"
            id="customCheck"
          />
          <label
            className="custom-control-label d-flex flex-grow-1"
            htmlFor="customCheck"
          >
            <input
              type="text"
              className="form-control mr-2"
              placeholder="parameter"
            />
            <input type="text" className="form-control" placeholder="value" />
          </label>
          <label className="ps-2">
            <i className="fa fa-times" aria-hidden="true"></i>
          </label>
        </div> */}
        {queryParams?.length > 0 &&
          queryParams.map((v, i) => {
            return <Fragment key={i}>{queryParamsRow(v, i)}</Fragment>
          })}
      </div>
    )
  }

  const handleOnChange = (e, i) => {
    const { value, name } = e.target
    const headersNew = [...headers]
    if (i === headersNew.length - 1) {
      headersNew.push({ fieldName: "", value: "", checked: false })
    }
    if (name === "checked") {
      headersNew[i][name] = !headersNew[i].checked
    } else {
      headersNew[i][name] = value
    }
    setHeaders(headersNew)
  }

  const ContentTabHeaders = () => (
    <div className="a-headers a-query ">
      <div className="d-flex justify-content-between">
        <h4>Http Headers</h4>
        <div className="custom-control custom-checkbox mb-3">
          <input
            type="checkbox"
            className="custom-control-input"
            id="customCheckRaw"
          />
          <label className="custom-control-label" htmlFor="customCheckRaw">
            Raw
          </label>
        </div>
      </div>
      {headers.map((item, i) => {
        return (
          <div className="custom-control a-query-row form-inline d-flex align-items-center flex-nowrap custom-checkbox mb-3">
            <input
              type="checkbox"
              className="custom-control-input"
              defaultChecked={item.checked}
              name="checked"
              id={`customCheckHeader${i}`}
              onChange={e => {
                handleOnChange(e, i)
              }}
            />
            <label
              className="custom-control-label d-flex flex-grow-1"
              htmlFor={`customCheckHeader${i}`}
            >
              <input
                type="text"
                defaultValue={item.fieldName}
                value={item.fieldName}
                className="form-control mr-2"
                name={`fieldName`}
                placeholder="header"
                onChange={e => {
                  handleOnChange(e, i)
                }}
              />
              <input
                type="text"
                defaultValue={item.value}
                value={item.value}
                name={`value`}
                className="form-control"
                placeholder="value"
                onChange={e => {
                  handleOnChange(e, i)
                }}
              />
            </label>
            {headers.length - 1 !== i ? (
              <label
                className="ps-2"
                onClick={() => {
                  setHeaders(headers.filter((item, index) => index !== i))
                }}
              >
                <i className="fa fa-times" aria-hidden="true"></i>
              </label>
            ) : (
              <label className="ps-2"></label>
            )}
          </div>
        )
      })}
    </div>
  )
  const ContentTabAuth = data => (
    <div className="a-auth">
      <Tabs
        tabsWrapperClass="body-tabs body-tabs-layout"
        transform={false}
        showInkBar={true}
        items={getTabs(data.Tabs)}
        selectedTabKey={fnListMyPage.getTabsIndexSelected(cfp.fnList)}
        onChange={index => {
          // console.warn("onChange:", index)
          fnListMyPage.onChangeTab(cfp.fnList, [index])
        }}
      />
    </div>
  )
  const ContentTabBody = data => (
    <div>
      <div className="a-auth">
        <Tabs
          tabsWrapperClass="body-tabs body-tabs-layout"
          transform={false}
          showInkBar={true}
          items={getTabs(data.Tabs)}
          selectedTabKey={fnListMyPage.getTabsIndexSelected(cfp.fnList)}
          onChange={index => {
            // console.warn("onChange:", index)
            fnListMyPage.onChangeTab(cfp.fnList, [index])
          }}
        />
      </div>
    </div>
  )
  const ContentTabTests = () => <div>ContentTabTests</div>

  // Tab of auth
  const ContentTabNone = () => (
    <div>
      <h4 className="ms-4 my-4">No Authentication Selected</h4>
    </div>
  )
  const ContentTabBasic = () => (
    <div className="a-basic ">
      <h4 className="my-4">Basic Authentication</h4>
      <div className="form-group d-flex align-items-center">
        <label htmlFor="username" className="me-2">
          Username
        </label>
        <input
          type="username"
          className="form-control"
          placeholder="username"
          id="username"
        />
      </div>
      <div className="form-group a-pwd d-flex align-items-center">
        <label htmlFor="pwd" className="me-2">
          Password
        </label>
        <input
          type="password"
          className="form-control"
          placeholder="password"
          id="pwd"
        />
        <i className="fa fa-lock"></i>
      </div>
    </div>
  )
  const ContentTabBearer = () => (
    <div className="a-bearer ">
      <h4 className="my-4">Bearer Token</h4>
      <textarea
        placeholder="enter token"
        value={beareToken}
        onChange={e => {
          setBeareToken(e.target.value)
        }}
      ></textarea>
      <div className="form-group d-flex align-items-center">
        <label htmlFor="token" className="me-2">
          Token Prefix
        </label>
        <input
          type="text"
          className="form-control"
          placeholder="Bearer"
          // defaultValue="Bearer"
          value={tokenPrefix}
          onChange={e => {
            setTokenPrefix(e.target.value)
          }}
        />
      </div>
    </div>
  )
  const ContentTabOAuth2 = () => {
    return (
      <div className="a-oauth2 ">
        <h4 className="my-4">OAuth Authentication</h4>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="access-token" className="me-2">
            Access Token
          </label>
          <input
            type="text"
            className="form-control"
            placeholder="auto generated"
          />
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="token-prefix" className="me-2">
            Token Prefix
          </label>
          <input
            type="text"
            className="form-control"
            placeholder="Bearer"
            value="Bearer"
            onChange={() => {}}
          />
        </div>
        <p>
          The generated oauth token will be automatically added to Autorization
          header
        </p>
        <h4 className="my-4">OAuth Authentication</h4>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="access-token" className="me-2">
            Grant Type
          </label>
          <select className="form-control">
            <option>Authorization Code</option>
            <option>Authorization Code with PKCE</option>
            <option>Client Credentials</option>
            <option>Password Credentials</option>
          </select>
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="access-token" className="me-2">
            Auth Url
          </label>
          <input type="text" className="form-control" placeholder="auth url" />
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="token-prefix" className="me-2">
            Token Url
          </label>
          <input type="text" className="form-control" placeholder="token url" />
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="access-token" className="me-2">
            Auth Url
          </label>
          <input type="text" className="form-control" placeholder="auth url" />
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="token-prefix" className="me-2">
            Callback Url
          </label>
          <input
            type="text"
            className="form-control"
            placeholder="callback url"
          />
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="access-token" className="me-2">
            Client ID
          </label>
          <input type="text" className="form-control" placeholder="client id" />
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="token-prefix" className="me-2">
            Client Secret
          </label>
          <input
            type="text"
            className="form-control"
            placeholder="client secret"
          />
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="access-token" className="me-2">
            Scope
          </label>
          <input type="text" className="form-control" placeholder="scope" />
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="token-prefix" className="me-2">
            State
          </label>
          <input type="text" className="form-control" placeholder="state" />
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="token-prefix" className="me-2">
            Send Auth
          </label>
          <select className="form-control">
            <option>Authorization Code</option>
            <option>Authorization Code with PKCE</option>
            <option>Client Credentials</option>
            <option>Password Credentials</option>
          </select>
        </div>
        <h4 className="my-4">Advanced Options</h4>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="token-prefix" className="me-2">
            Audience
          </label>
          <input
            type="text"
            className="form-control"
            placeholder="audience(optional)"
          />
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="token-prefix" className="me-2">
            Resource
          </label>
          <input
            type="text"
            className="form-control"
            placeholder="resource(optional)"
          />
        </div>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="token-prefix" className="me-2">
            Token Name
          </label>
          <input
            type="text"
            className="form-control"
            placeholder="token property name"
            value="access_token"
            onChange={() => {}}
          />
        </div>
      </div>
    )
  }
  const ContentTabNtlm = () => {
    return (
      <div className="a-oauth2 ">
        <h4 className="my-4">NTLM Authentication</h4>
        <div className="form-group d-flex align-items-center">
          <label htmlFor="username" className="me-2">
            Username
          </label>
          <input
            type="text"
            className="form-control"
            placeholder="username"
            id="username"
          />
        </div>
        <div className="form-group a-pwd d-flex align-items-center">
          <label htmlFor="pwd" className="me-2">
            Password
          </label>
          <input
            type="password"
            className="form-control"
            placeholder="password"
            id="pwd"
          />
          <i className="fa fa-lock"></i>
        </div>
        <div className="form-group d-flex align-items-center">
          <label className="me-2">Workstation</label>
          <input
            type="text"
            className="form-control"
            placeholder="workstation (optional)"
          />
        </div>
        <div className="form-group d-flex align-items-center">
          <label className="me-2">Domain</label>
          <input
            type="text"
            className="form-control"
            placeholder="domain (optional)"
            onChange={() => {}}
          />
        </div>
      </div>
    )
  }
  const ContentTabAws = () => {
    return (
      <div className="a-oauth2 ">
        <h4 className="my-4">AWS v4 Signature</h4>
        <div className="form-group d-flex align-items-center">
          <label className="me-2">Access Key</label>
          <input
            type="text"
            className="form-control"
            placeholder="access key id"
          />
        </div>
        <div className="form-group d-flex align-items-center">
          <label className="me-2">Secret Key</label>
          <input
            type="text"
            className="form-control"
            placeholder="secret key id"
          />
        </div>
        <div className="form-group d-flex align-items-center">
          <label className="me-2">Region</label>
          <input
            type="text"
            className="form-control"
            placeholder="aws region"
          />
        </div>
        <div className="form-group d-flex align-items-center">
          <label className="me-2">Service</label>
          <input
            type="text"
            className="form-control"
            placeholder="aws service"
            onChange={() => {}}
          />
        </div>
        <div className="form-group d-flex align-items-center">
          <label className="me-2">Session Token</label>
          <input
            type="text"
            className="form-control"
            placeholder="session token"
            onChange={() => {}}
          />
        </div>
      </div>
    )
  }
  // Tab of body
  const ContentTabJson = () => (
    <div className="a-json ">
      <h4 className="my-4">Json Content</h4>
      <MyLib
        type={"code_mirror"}
        innerRef={r => {
          console.warn("innerRef")
          refCode2.current = r
        }}
        value={JSON.stringify(json, null, 2)}
        options={{
          // mode: "javascript",
          mode: {
            name: "javascript",
            json: true,
            statementIndent: 2,
          },
          lineNumbers: true,
          lineWrapping: true,
          indentWithTabs: false,
          tabSize: 2,
          theme: "default",
        }}
      />
    </div>
  )
  const ContentTabXml = () => (
    <div className="a-json ">
      <h4 className="my-4">Xml Content</h4>
      <MyLib
        type={"code_mirror"}
        innerRef={r => {
          console.warn("innerRef")
          refCode2.current = r
        }}
        value={JSON.stringify(json, null, 2)}
        options={{
          // mode: "javascript",
          mode: {
            name: "javascript",
            json: true,
            statementIndent: 2,
          },
          lineNumbers: true,
          lineWrapping: true,
          indentWithTabs: false,
          tabSize: 2,
          theme: "default",
        }}
      />
    </div>
  )
  const ContentTabText = () => (
    <div className="a-text ">
      <h4 className="my-4">Text Content</h4>
      <textarea
        placeholder="enter text"
        // value={beareToken}
        // onChange={e => {
        //   setBeareToken(e.target.value)
        // }}
      ></textarea>
    </div>
  )
  const ContentTabForm = () => (
    <div className="a-query ">
      <div className="d-flex justify-content-between">
        <h4>Form Fields</h4>
        <div className="custom-control custom-checkbox mb-3">
          <input
            type="checkbox"
            className="custom-control-input"
            id="customCheckFiles"
          />
          <label className="custom-control-label" htmlFor="customCheckFiles">
            Files
          </label>
        </div>
      </div>
      <div className="custom-control a-query-row form-inline d-flex align-items-center flex-nowrap custom-checkbox mb-3">
        <input
          type="checkbox"
          className="custom-control-input"
          id="customCheck"
          name="example1"
        />
        <label
          className="custom-control-label d-flex flex-grow-1"
          htmlFor="customCheck"
        >
          <input
            type="text"
            className="form-control mr-2"
            placeholder="field name"
          />
          <input type="text" className="form-control" placeholder="value" />
        </label>
        <label className="ps-2">
          <i className="fa fa-times" aria-hidden="true"></i>
        </label>
      </div>
    </div>
  )
  const ContentTabFormEncode = () => (
    <div className="a-query ">
      <h4>Form Encoded</h4>
      <div className="custom-control a-query-row form-inline d-flex align-items-center flex-nowrap custom-checkbox mb-3">
        <input
          type="checkbox"
          className="custom-control-input"
          id="customCheck"
          name="example1"
        />
        <label
          className="custom-control-label d-flex flex-grow-1"
          htmlFor="customCheck"
        >
          <input type="text" className="form-control mr-2" placeholder="name" />
          <input type="text" className="form-control" placeholder="value" />
        </label>
        <label className="ps-2">
          <i className="fa fa-times" aria-hidden="true"></i>
        </label>
      </div>
    </div>
  )
  const ContentTabGraphql = () => (
    <div className="a-json ">
      <h4 className="my-4">Query</h4>
      <MyLib
        type={"code_mirror"}
        innerRef={r => {
          console.warn("innerRef")
          refCodeQuery.current = r
        }}
        value={JSON.stringify(json, null, 2)}
        options={{
          // mode: "javascript",
          mode: {
            name: "javascript",
            json: true,
            statementIndent: 2,
          },
          lineNumbers: true,
          lineWrapping: true,
          indentWithTabs: false,
          tabSize: 2,
          theme: "default",
        }}
      />
      <h4 className="my-4">Variables</h4>
      <MyLib
        type={"code_mirror"}
        innerRef={r => {
          console.warn("innerRef")
          refCodeVar.current = r
        }}
        value={JSON.stringify(json, null, 2)}
        options={{
          // mode: "javascript",
          mode: {
            name: "javascript",
            json: true,
            statementIndent: 2,
          },
          lineNumbers: true,
          lineWrapping: true,
          indentWithTabs: false,
          tabSize: 2,
          theme: "default",
        }}
      />
    </div>
  )
  const ContentTabBinary = () => (
    <div className="a-text  my-4">
      <h4 className="my-4">Binary File</h4>
      <input type="file" className="form-control-file border" />
    </div>
  )
  const tabsList = [
    // {
    //   Id: "tab_1",
    //   Title: "Query",
    //   Contents: ContentTabQuery,
    // },
    {
      Id: "tab_2",
      Title: "Headers",
      Contents: ContentTabHeaders,
    },
    {
      Id: "tab_3",
      Title: "Auth",
      Contents: ContentTabAuth,
      Data: {
        Tabs: [
          {
            Id: "tab_1",
            Title: "None",
            Contents: ContentTabNone,
          },
          {
            Id: "tab_2",
            Title: "Basic",
            Contents: ContentTabBasic,
          },
          {
            Id: "tab_3",
            Title: "Bearer",
            Contents: ContentTabBearer,
          },
          {
            Id: "tab_4",
            Title: "OAuth 2",
            Contents: ContentTabOAuth2,
          },
          {
            Id: "tab_5",
            Title: "Ntlm",
            Contents: ContentTabNtlm,
          },
          {
            Id: "tab_6",
            Title: "Aws",
            Contents: ContentTabAws,
          },
        ],
      },
    },
    {
      Id: "tab_4",
      Title: "Body",
      Contents: ContentTabBody,
      Data: {
        Tabs: [
          {
            Id: "tab_1",
            Title: "Json",
            Contents: ContentTabJson,
          },
          {
            Id: "tab_2",
            Title: "Xml",
            Contents: ContentTabXml,
          },
          {
            Id: "tab_3",
            Title: "Text",
            Contents: ContentTabText,
          },
          {
            Id: "tab_4",
            Title: "Form",
            Contents: ContentTabForm,
          },
          {
            Id: "tab_5",
            Title: "Form-encode",
            Contents: ContentTabFormEncode,
          },
          {
            Id: "tab_6",
            Title: "Graphql",
            Contents: ContentTabGraphql,
          },
          {
            Id: "tab_7",
            Title: "Binary",
            Contents: ContentTabBinary,
          },
        ],
      },
    },
    // {
    //   Id: "tab_5",
    //   Title: "Tests",
    //   Contents: ContentTabTests,
    // },
  ]
  const getTabs = list => {
    return list.map((v, index) => ({
      title: v.Title,
      getContent: () => {
        return <Fragment>{v.Contents(v.Data)}</Fragment>
      },
    }))
  }
  const bytesToSize = bytes => {
    var sizes = ["Bytes", "KB", "MB", "GB", "TB"]
    if (bytes == 0) return "0 Byte"
    var i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)))
    return Math.round((bytes / Math.pow(1024, i)) * 100) / 100 + " " + sizes[i]
  }
  const roughSizeOfObject = object => {
    var objectList = []

    var recurse = function (value) {
      var bytes = 0

      if (typeof value === "boolean") {
        bytes = 4
      } else if (typeof value === "string") {
        bytes = value.length * 2
      } else if (typeof value === "number") {
        bytes = 8
      } else if (
        typeof value === "object" &&
        objectList.indexOf(value) === -1
      ) {
        objectList[objectList.length] = value

        for (let i in value) {
          bytes += 8 // an assumed existence overhead
          bytes += recurse(value[i])
        }
      }

      return bytes
    }
    return recurse(object)
  }
  const handleSubmit = () => {
    let data =
      method === "GET"
        ? {}
        : refCode2?.current?.editor.getValue() || JSON.stringify(json)
    if (method !== "GET") {
      try {
        JSON.parse(data)
      } catch (e) {
        alert("Invalid Json Body")
        return
      }
    }
    const newHeaders = headers.reduce((object, item) => {
      if (item.checked) {
        return { ...object, [item.fieldName]: item.value }
      }
      return { ...object }
    }, {})
    let header = {
      ...newHeaders,
      Authorization: tokenPrefix + " " + beareToken,
    }
    var sendDate = new Date().getTime()
    ApiGeneric({
      request: {
        method: method,
        url: url,
      },
      customHeader: header || {},
      data: data || {},
      successCallBack: response => {
        console.warn("response", response)
        var receiveDate = new Date().getTime()
        var responseTimeMs = receiveDate - sendDate
        const Size = bytesToSize(roughSizeOfObject(response))
        setVRes(response)
        setReponseResult({
          ...responseResult,
          StatusCode: response.StatusCode,
          Size: Size,
          Times: responseTimeMs,
        })
      },
      errorCallBack: (error, response) => {
        console.error("err", error, response)
        var receiveDate = new Date().getTime()
        var responseTimeMs = receiveDate - sendDate
        setReponseResult({
          ...responseResult,
          StatusCode: error.response?.status,
          Size: error.response?.headers["content-length"]
            ? bytesToSize(error.response?.headers["content-length"])
            : bytesToSize(roughSizeOfObject(error.response?.data || {})),
          Times: responseTimeMs,
        })
        setVRes(error.response?.data)
      },
    })
  }
  return (
    <div className="mp-api_request">
      <WrapApiRequest>
        <div className="row">
          <div className="col-lg-6 col-12 border border-secondary">
            <div className="input-group mt-3 mb-3">
              <div className="input-group-prepend">
                <select
                  value={method}
                  onChange={e => {
                    setMethod(e.target.value)
                  }}
                >
                  <option value="GET">GET</option>
                  <option value="POST">POST</option>
                  <option value="PUT">PUT</option>
                  <option value="DELETE">DELETE</option>
                </select>
              </div>
              <input
                type="text"
                className="form-control"
                placeholder="Url"
                value={url}
                onChange={e => {
                  setUrl(e.target.value)
                }}
              />
              <div className="input-group-append">
                <button
                  className="btn btn-primary"
                  type="button"
                  onClick={() => handleSubmit()}
                >
                  Send
                </button>
              </div>
            </div>
            <div className="a-request-container">
              <Tabs
                tabsWrapperClass="body-tabs body-tabs-layout"
                transform={false}
                showInkBar={true}
                items={getTabs(tabsList)}
                selectedTabKey={fnListMyPage.getTabsIndexSelected(cfp.fnList)}
                onChange={index => {
                  // console.warn("onChange:", index)
                  fnListMyPage.onChangeTab(cfp.fnList, [index])
                }}
              />
            </div>
          </div>
          <div className="col-lg-6 col-12 border border-secondary px-0">
            <div className="a-status-bar mt-3 mb-3">
              <span>
                Status:{" "}
                <span
                  className={`${
                    StatusCodeArr.find(item => {
                      return item.code == responseResult.StatusCode
                    })?.colorCode
                  }`}
                >
                  {responseResult.StatusCode}
                </span>
              </span>
              <span>
                Size:{" "}
                <span className="text-success">
                  {responseResult.Size ? responseResult.Size : ""}
                </span>
              </span>
              <span>
                Time:{" "}
                <span className="text-success">
                  {responseResult.Times || responseResult.Times === 0
                    ? responseResult.Times + " ms"
                    : ""}
                </span>
              </span>
            </div>
            <h5 className="px-2 pt-2">Response</h5>
            {vRes !== "" ? (
              <MyLib
                type={"code_mirror"}
                innerRef={r => {
                  console.warn("innerRef")
                  refCode.current = r
                }}
                value={JSON.stringify(vRes, null, 2)}
                options={{
                  // mode: "javascript",
                  mode: {
                    name: "javascript",
                    json: true,
                    statementIndent: 2,
                  },
                  lineNumbers: true,
                  lineWrapping: true,
                  indentWithTabs: false,
                  tabSize: 2,
                  theme: "default",
                }}
              />
            ) : (
              <p className="text-center my-3">No response</p>
            )}
          </div>
        </div>
      </WrapApiRequest>
    </div>
  )
}
